import {isEmail,isAlphanumeric,isLength} from 'validator';
import debounce from 'lodash/debounce';

export let AuthActions;
export let password;
const setAuthActions = (authActions) => {
    AuthActions = authActions;
}
export {setAuthActions};
const setPassword = (Password) => {
    password = Password;
}
export {setPassword};
const setError = (message,id) => {
    AuthActions.setError({form: 'register',message});
    AuthActions.setErrorId({form: 'register',id});
};
export {setError};
export const validate = {
    email: (value) => {
        if(!isEmail(value)){
            setError('   .','email');
            return false;
        }
        return true;
    },
        id: (value) => {
        if(!isAlphanumeric(value) || !isLength(value, {min:9, max: 15})) {
            setError(' 9~15      .','id');
            return false;
        }
        return true;
    },
    password: (value) => {
        
        if(!new RegExp(/^(?=.*\d)(?=.*[~`!@#$%\\^&*()-])(?=.*[a-z])(?=.*[A-Z]).{9,12}$/).test(value)){
            setError(' 9~12   ( ), ,   .','password');
            return false;
        }
        else if(new RegExp(/(\w)\1\1\1/).test(value)){
            setError('   4    .','password');
            return false;
        }
        setError(null);
        return true;
    },
    passwordConfirm: (value) => {
        if(password !== value){
            setError('   .','passwordConfirm');
            return false;
        }
        setError(null);
        return true;
    },
    phone: (value)=> {
        if(!new RegExp(/^01(?:0|1|[6-9])-(\d{3}|\d{4})-(\d{4})$/).test(value)){
           setError('  01x-xxx(x)-xxxx    .','phone');
            return false;
        }
        return true;
    },
    comment: (value)=> {
        if(!isLength(value, {min:0, max: 50})){
            setError(' 50   .','comment');
            return false;
        }
        setError(null);
        return true;
    },
    name: (value) => {
        if(!isLength(value, {min:1, max: 30})){
            setError(' 1  30  .','name');
            return false;
        }
        setError(null);
        return true;
    },
    gender: (value) => {
        if(value == null){
            setError('   .','gender');
            return false;
        }
        setError(null);
        return true;
    }

};

export const checkEmailExists = debounce(async (email) => {
    try{
        await AuthActions.checkEmailExists(email,'null');
        setError(null,'email');
    }catch(e){
        setError('  .','email');
    }
},300);

export const checkIdExists = debounce(async(id)=> {
    try{
        await AuthActions.checkIdExists(id);
            setError(null,'id');
        
    } catch (e){
        setError('  .','id');
    }
},300);

export const checkPhoneExists = debounce(async(phone) => {
    try{
        await AuthActions.checkPhoneExists(phone,'null');
            setError(null,'phone');
        
    }catch (e){
        setError('   .','phone');
    }
},300);

export const inputStyle = {
    width: '15%',
    left: '5%',
    position: 'relative',
outline: 'none',
borderRadius: '0px',
lineHeight: '2.5rem',
fontSize: '1.2rem',
paddingLeft: '0.5rem',
fontFamily: 'Noto Sans KR',
fontStyle: 'normal',
paddingRight: '1rem',
marginRight : '1rem'
}
